const dcByLevel = new Map<number, number>()
dcByLevel.set(-1, 13)
dcByLevel.set(0, 14)
dcByLevel.set(1, 15)
dcByLevel.set(2, 16)
dcByLevel.set(3, 18)
dcByLevel.set(4, 19)
dcByLevel.set(5, 20)
dcByLevel.set(6, 22)
dcByLevel.set(7, 23)
dcByLevel.set(8, 24)
dcByLevel.set(9, 26)
dcByLevel.set(10, 27)
dcByLevel.set(11, 28)
dcByLevel.set(12, 30)
dcByLevel.set(13, 31)
dcByLevel.set(14, 32)
dcByLevel.set(15, 34)
dcByLevel.set(16, 35)
dcByLevel.set(17, 36)
dcByLevel.set(18, 38)
dcByLevel.set(19, 39)
dcByLevel.set(20, 40)
dcByLevel.set(21, 42)
dcByLevel.set(22, 44)
dcByLevel.set(23, 46)
dcByLevel.set(24, 48)
dcByLevel.set(25, 50)

const dcAdjustments = new Map<DCAdjustment, number>()
dcAdjustments.set('incredibly easy', -10)
dcAdjustments.set('very easy', -5)
dcAdjustments.set('easy', -2)
dcAdjustments.set('normal', 0)
dcAdjustments.set('hard', 2)
dcAdjustments.set('very hard', 5)
dcAdjustments.set('incredibly hard', 10)

/**
 * Normal Level Based DCs
 * @param level
 * @param proficiencyWithoutLevel
 */
export function calculateDC(level: number, { proficiencyWithoutLevel = false, rarity = 'common' }: DCOptions = {}): number {
    // assume level 0 if garbage comes in. We cast level to number because the backing data may actually have it
    // stored as a string, which we can't catch at compile time
    const dc = dcByLevel.get(level) ?? 14
    if (proficiencyWithoutLevel) {
        // -1 shouldn't be subtracted since it's just
        // a creature level and not related to PC levels
        return adjustDCByRarity(dc - Math.max(level, 0), rarity)
    } else {
        return adjustDCByRarity(dc, rarity)
    }
}

export function adjustDCByRarity(dc: number, rarity: Rarity = 'common') {
    return adjustDC(dc, rarityToDCAdjustment(rarity))
}

export function adjustDC(dc: number, adjustment: DCAdjustment = 'normal') {
    return dc + (dcAdjustments.get(adjustment) ?? 0)
}

export function rarityToDCAdjustment(rarity: Rarity = 'common'): PositiveDCAdjustment {
    if (rarity === 'uncommon') {
        return 'hard'
    } else if (rarity === 'rare') {
        return 'very hard'
    } else if (rarity === 'unique') {
        return 'incredibly hard'
    } else {
        return 'normal'
    }
}
