import { adjustDCByRarity, calculateDC } from '@utils/pf2e/dc'
import { MAGIC_TRADITIONS } from '@utils/pf2e/spell'
import { setHasElement } from '@utils/pf2e/utils'

export function identifyItem(
    item: PhysicalItemPF2e,
    { proficiencyWithoutLevel = false, notMatchingTraditionModifier }: IdentifyItemOptions
): GenericIdentifyDCs | IdentifyMagicDCs | IdentifyAlchemyDCs {
    const baseDC = calculateDC(item.level, { proficiencyWithoutLevel })
    const rarity = getDcRarity(item)
    const dc = adjustDCByRarity(baseDC, rarity)
    if (item.isMagical) {
        return identifyMagic(item, dc, notMatchingTraditionModifier)
    } else if (item.isAlchemical) {
        return { cra: dc }
    } else {
        return { dc: dc }
    }
}

/** All cursed items are incredibly hard to identify */
function getDcRarity(item: PhysicalItemPF2e): Rarity {
    return item.traits.has('cursed') ? 'unique' : item.rarity
}

function identifyMagic(item: PhysicalItemPF2e, baseDc: number, notMatchingTraditionModifier: number) {
    const result = {
        occult: baseDc,
        primal: baseDc,
        divine: baseDc,
        arcane: baseDc,
    }
    const traditions = getMagicTraditions(item)
    for (const key of MAGIC_TRADITIONS) {
        // once an item has a magic tradition, all skills
        // that don't match the tradition are hard
        if (traditions.size > 0 && !traditions.has(key)) {
            result[key] = baseDc + notMatchingTraditionModifier
        }
    }
    return { arc: result.arcane, nat: result.primal, rel: result.divine, occ: result.occult }
}

/**
 * Extract all traits from an item, that match a magic tradition
 */
function getMagicTraditions(item: PhysicalItemPF2e): Set<MagicTradition> {
    const traits: string[] = item.system.traits.value
    return new Set(traits.filter((t): t is MagicTradition => setHasElement(MAGIC_TRADITIONS, t)))
}

export function getChoiSetRuleSelection<T extends unknown>(item: ItemPF2e, option: string) {
    const rules = item._source.system.rules as ChoiceSetSource[]
    const rule = rules.find(rule => rule.key === 'ChoiceSet' && rule.rollOption === option)
    return rule?.selection as T | undefined
}

const RUNE_PROPERTY_KEYS = ['propertyRune1', 'propertyRune2', 'propertyRune3', 'propertyRune4'] as const

export function getFreePropertyRuneSlot(item: WeaponPF2e | ArmorPF2e): null | typeof RUNE_PROPERTY_KEYS[number] {
    const potency = item.system.potencyRune.value
    if (potency === null) return null

    for (let i = 0; i < potency; i++) {
        const property = RUNE_PROPERTY_KEYS[i]
        if (!item.system[property].value) return property
    }

    return null
}

const PHYSICAL_ITEM_TYPES = new Set(['armor', 'backpack', 'book', 'consumable', 'equipment', 'treasure', 'weapon'] as const)

/** Checks if the given item data is a physical item with a quantity and other physical fields. */
export function isPhysicalData(source: ItemSourcePF2e): source is PhysicalItemSource
export function isPhysicalData(source: PreCreate<ItemSourcePF2e>): source is PreCreate<PhysicalItemSource>
export function isPhysicalData(source: ItemSourcePF2e | PreCreate<ItemSourcePF2e>): boolean {
    return setHasElement(PHYSICAL_ITEM_TYPES, source.type)
}

export function hasInvestedProperty(source: ItemSourcePF2e): source is MagicItemSource {
    return isPhysicalData(source) && 'invested' in source.system.equipped
}
