import { MODULE_ID } from '@utils/module'

export function socketOn<T extends SocketPacket = SocketPacket>(callback: SocketCallback<T>) {
    game.socket.on(`module.${MODULE_ID}`, callback)
}

export function socketEmit<T extends SocketPacket = SocketPacket>(packet: T) {
    game.socket.emit(`module.${MODULE_ID}`, packet)
}
